export default {
	data() {
		return {
			hasText: false,
			searchWords: '',
			focusFlag: false,
			hasSearchInfo: false,
			historyWordsList: [],
			searchList: [],
			searchInfo: []
		};
	},
	onLoad(options) {
		console.log(options)
		// 判断是否有搜索关键字
		let arr = Object.keys(options)
		console.log(arr.length)
		if (arr.length !== 0) {
			this.searchWords = options.keyWords;
			console.log(this.searchWords)
			this.focusFlag = true
			this.hasText = true;
			this.getSearchList()
		} else {
			this.hasText = false;
		}
		// 获取历史搜索记录
		this.historyWordsList = uni.getStorageSync('history_record') || []
		console.log(this.historyWordsList)
	},
	methods: {
		// 输入框获取关键词
		inputText(e) {
			console.log(e.detail);
			if (e.detail.value !== '') {
				this.hasText = true;
				this.searchWords = e.detail.value;
				this.getSearchList();
			} else {
				this.hasText = false;
			}
		},
		// 获得模糊搜索数据
		getSearchList() {
			console.log(this.searchList);
			this.$api.searchName({
				keywords: this.searchWords
			}).then((res) => {
				// console.log(res)
				if (res.data.errno === 0) {
					this.searchList = res.data.data
					this.searchInfo = []
					if (this.searchList.length === 0) {
						this.hasSearchInfo = false
					} else {
						this.hasSearchInfo = true
						this.searchList.map((item, index) => {
							item = item.replace(this.searchWords, '');
							this.searchInfo.push(item)
						})
					}
				}
			})
		},
		// 删除关键词
		deleteSearchWords() {
			console.log('点击了删除');
			this.hasText = false;
			this.searchWords = '';
			this.focusFlag = true
		},
		// 删除历史记录
		deleteHistory() {
			uni.setStorageSync('history_record', '')
			this.historyWordsList = []
		},
		// 从模糊搜索获取关键词
		getWords(words) {
			// console.log(words)
			this.searchWords = this.searchWords + words;
			this.getSearchList()
		},
		// 从历史激励获取关键词
		getSearch(words) {
			console.log(words)
			this.searchWords = words
			this.hasText = true
			this.focusFlag = true
			this.getSearchList()
		},
		// 点击搜索跳转到详情页
		toSearchDetail() {
			console.log(this.searchWords === '')
			if (this.searchWords !== '') {
				// 将当前搜索关键字存入缓存(如果有相同的则先删除已有的再插入到最前面)
				if (this.historyWordsList.length !== 0) {
					let index = this.historyWordsList.indexOf(this.searchWords);
					console.log(index)
					// 获得的index进行操作（如果index为-1，代表没有找到，否则返回的是找到的元素的下标）
					if (index > -1) {
						this.historyWordsList.splice(index, 1);
					}
				}
				
				this.historyWordsList.unshift(this.searchWords)
				uni.setStorageSync('history_record', this.historyWordsList)
				// 跳转
				uni.navigateTo({
					url: '/pages/index/searchDetail?searchKey=' + this.searchWords
				})
			}else{
				uni.showToast({
					title: '请输入关键字',
					duration: 1000,
					icon: 'none'
				})
			}
		}
	}
}
